.mainContent { flex-grow: 1; padding: 40px; background-color: #f9fbfc; }
.welcomeHeader { margin-bottom: 40px; }
.welcomeHeader h1 { font-size: 28px; font-weight: 800; color: #2c3e50; }
.welcomeHeader p { font-size: 16px; color: #555; }

.dashboardGrid { display: grid; grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)); gap: 30px; }

.dashboardCard { background: #fff; border-radius: 15px; padding: 25px; box-shadow: 0 5px 20px rgba(0,0,0,0.07); text-decoration: none; color: inherit; transition: all 0.3s ease; }
.dashboardCard:hover { transform: translateY(-5px); box-shadow: 0 8px 25px rgba(0,0,0,0.1); }

.cardIcon { font-size: 36px; margin-bottom: 15px; color: #4a6cfa; }
.dashboardCard h3 { font-size: 18px; font-weight: 700; color: #2c3e50; margin-bottom: 10px; }
.dashboardCard p { font-size: 14px; color: #777; line-height: 1.5; }