.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 250px; /* Fixed width */
    height: 100vh; /* Full viewport height */
    background-color: #ffffff;
    padding: 25px;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #e0e0e0;
    box-sizing: border-box;
    z-index: 1000; /* Ensure it's above content */
}
.logo { font-size: 26px; font-weight: 800; color: #333; margin-bottom: 40px; text-align: center; }
.sidebar ul { list-style: none; padding: 0; margin: 0; flex-grow: 1; }
.sidebar li { margin-bottom: 8px; }
.sidebar a { text-decoration: none; color: #555; display: block; padding: 16px 12px; border-radius: 8px; transition: background-color 0.2s, color 0.2s; }
.sidebar a:hover { background-color: #f0f4ff; }
.sidebar a.active { background-color: #4a6cfa; color: white; font-weight: 600; }
.userProfile { margin-top: auto; border-top: 1px solid #eee; padding-top: 20px; text-align: center; }
.userInfo { margin-bottom: 15px; }
.userName { display: block; font-weight: 600; font-size: 15px; }
.userId { display: block; font-size: 13px; color: #777; }
.logoutButton { width: 100%; padding: 12px; border: 1px solid #dcdcdc; background: #fdfdfd; border-radius: 8px; cursor: pointer; font-size: 14px; transition: background-color 0.2s; }
.logoutButton:hover { background-color: #f5f5f5; }